<template>
  <div class="gameFooterSettings">
    <GameFooterColors
      :color.sync="game.settings.color"
      class="gameFooterSettings_select"
      :class="{'gameFooterSettings_select-active' : select === 'color'}"
      v-on:select="update"
    />
    <GameFooterValue
      :value.sync="game.settings.size"
      class="gameFooterSettings_select"
      :class="{'gameFooterSettings_select-active' : select === 'size'}"
      type="size"
      v-on:select="update"
    />
    <GameFooterValue
      :value.sync="game.settings.players"
      class="gameFooterSettings_select"
      :class="{'gameFooterSettings_select-active' : select === 'players'}"
      type="players"
      v-on:select="update"
    />
    <GameFooterValue
      :value.sync="game.settings.difficulty"
      class="gameFooterSettings_select"
      :class="{'gameFooterSettings_select-active' : select === 'difficulty'}"
      type="difficulty"
      v-on:select="update"
    />
    <div class="gameFooterSettings_content_container">
      <div class="gameFooterSettings_content">
        <v-btn
          text
          class="gameFooterSettings_link gameFooterSettings_link"
          @click="toSelect('color')"
        >
          <div
            class="gameFooterSettings_color"
            :class="['gameFooterSettings_color-' + game.settings.color]"
          ></div>
        </v-btn>
        <v-btn
          text
          class="gameFooterSettings_link gameFooterSettings_link"
          @click="toSelect('size')"
        >
          <div
            class="gameFooterSettings_link_icon icon icon-board"
            :class="['icon-board-' + game.settings.size]"
          ></div>
          <div class="gameFooterSettings_link_text">size</div>
        </v-btn>
        <v-btn
          text
          class="gameFooterSettings_link gameFooterSettings_link"
          @click="toSelect('players')"
        >
          <div
            class="gameFooterSettings_link_icon gameFooterSettings_link_icon-players icon icon-players"
          >
            <span class="gameFooterSettings_link_icon_text">{{game.settings.players}}</span>
          </div>
          <div class="gameFooterSettings_link_text">players</div>
        </v-btn>
        <v-btn
          text
          class="gameFooterSettings_link gameFooterSettings_link"
          @click="toSelect('difficulty')"
        >
          <div
            class="gameFooterSettings_link_icon icon icon-difficulty"
            :class="['icon-difficulty-' + game.settings.difficulty]"
          ></div>
          <div class="gameFooterSettings_link_text">level</div>
        </v-btn>
        <v-btn text class="gameFooterSettings_link gameFooterSettings_link" @click="newGame">
          <div class="gameFooterSettings_link_icon icon icon-dices">
            <span class="gameFooterSettings_link_icon_text"></span>
          </div>
          <div class="gameFooterSettings_link_text">play</div>
        </v-btn>
      </div>
    </div>
    <v-overlay :value="!!select" :z-index="1" @click.native="toSelect(null)"></v-overlay>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import GameService from '@/services/game.service'
import GameFooterColors from './GameFooterColors.vue'
import GameFooterValue from './GameFooterValue.vue'

export default {
  name: 'GameFooterSettings',
  components: {
    GameFooterColors,
    GameFooterValue
  },
  computed: mapState({
    settings: state => state.game.settings
  }),
  data: () => ({
    gameService: new GameService(),
    game: { settings: {} },
    select: null,
    color: 1
  }),
  mounted () {
    this.$set(this.game, 'settings', JSON.parse(JSON.stringify(this.settings)))
  },
  methods: {
    toSelect (val) {
      this.select = val
    },
    update () {
      setTimeout(
        function (scope) {
          scope.select = null
        },
        300,
        this
      )
    },
    newGame () {
      // this.changeSettings(this.game.settings)
      this.select = null
      this.gameService.newGame()
    },
    ...mapActions('game', ['changeSettings'])
  }
}
</script>

<style lang="scss">
.gameFooterSettings {
  &_select {
    position: absolute;
    left: 0;
    right: 0;
    bottom: -10px;
    height: 80px;
    background-color: rgba(0, 0, 0, 0.2);
    transition: all 0.2s cubic-bezier(0.4, 0, 0.6, 1);
    opacity: 0;
    z-index: 2;
    &-active {
      opacity: 1;
      bottom: 72px;
    }
  }
  &_content {
    &_container {
      width: 100%;
      background-color: #fff;
      position: relative;
      z-index: 2;
    }
    display: flex;
    max-width: 1024px;
    margin: 0 auto;
    justify-content: space-evenly;
    align-items: center;
    height: 72px;
  }
  &_link {
    &.v-btn:not(.v-btn--round).v-size--default {
      min-width: 64px;
      width: 20%;
      max-width: 80px;
      height: 72px;
      font-weight: 400;
    }
    .v-btn__content {
      flex-wrap: wrap;
      color: #c4c4c4;
    }
    &_text {
      font-size: 14px;
      line-height: 14px;
      width: 100%;
      text-transform: none;
    }
    &_icon {
      line-height: 34px;
      font-size: 30px;
      width: 100%;
      color: #e0e0e0;
      position: relative;
      color: rgba(0, 0, 0, 0.87);
      &_text {
        font-family: Roboto, sans-serif;
        font-size: 28px;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        color: rgba(0, 0, 0, 0.87);
      }
      &-players {
        color: #c4c4c4;
      }
      &-difficulty-0 {
        font-size: 24px;
      }
    }
  }
  &_color {
    width: 48px;
    height: 48px;
    border-radius: 4px;
    transition: background-color 0.1s cubic-bezier(0.4, 0, 0.6, 1);
    @each $i, $color in $colors {
      &-#{$i} {
        background-color: $color;
      }
    }
  }
}

.gameFooterSelect {
  padding: 0;
  margin: 0 auto;
  align-items: center;
  height: 80px;
  width: 100vw;
  text-align: center;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  &:after{
    content: '';
    display: block;
    position: absolute;
    left: calc(50% - 6px);
    bottom: -2px;
    height: 6px;
    width: 12px;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    background-color: #fff;
  }
  //scroll-snap-type: x mandatory;
  &_padding {
    width: calc(50vw - 40px);
    height: 80px;
    flex-shrink: 0;
    display: inline-block;
    vertical-align: top;
  }
  &_select {
    width: 80px;
    height: 80px;
    padding: 16px;
    text-align: center;
    line-height: 48px;
    font-size: 40px;
    color: #fff;
    //scroll-snap-align: center;
    display: inline-block;
    vertical-align: top;
    position: relative;
    &_content {
      width: 48px;
      height: 48px;
      transform-origin: center;
    }
    &_icon {
      position: relative;
      line-height: 48px;
    }
    &_text {
      position: absolute;
      line-height: 44px;
      font-size: 36px;
      color: rgba(0, 0, 0, 0.87);
      display: block;
      text-align: center;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      font-family: Roboto, sans-serif;
    }
    .icon-difficulty-0 {
      font-size: 35px;
    }
    .icon-difficulty-1 {
      font-size: 45px;
    }
  }
}
</style>
